.team-backdrop.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.stop-scroll {
    height: 100%;
    overflow: hidden;
}

.team-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000d4;
    visibility: visible;
    z-index: 6;
    opacity: 1;
    transition: opacity 450ms cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (max-width: 767px) {
        overflow-y: scroll;
    }
}

.team-modal {
    left: 50%;
    transform: translate(-50%, 50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.close {
    margin-left: auto;
    width: 30px;
    height: 30px;
}

.team-container {
    display: flex;
    flex-wrap: wrap;
}

.team-photo {
    position: relative;
    width: 100%;
}

.member-info {
    position: absolute;
    bottom: 0;
    background-color: #ff6b01;
    width: 100%;
    text-align: center;
}

.member-name {
    font-size: 16px;
    margin-bottom: 5px;

    @media screen and (min-width: 768px) {
        font-size: 18px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 20px;
    }
}

.member-pos {
    font-size: 12px;
    font-weight: 400;

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
    }
}

.swiper {
    width: 240px;
    height: 320px;
    margin-left: 0 !important;
    margin-right: 0 !important;

    @media screen and (min-width: 768px) {
        width: 320px;
    }

    @media screen and (min-width: 1200px) {
        width: 450px;
        height: 450px;
        padding: 50px;
    }
}

.swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    font-size: 22px;
    font-weight: bold;

    @media screen and (max-width: 767px) {
        height: 75% !important;
    }

    @media screen and (min-width: 1200px) {
        width: 450px !important;
        height: 450px !important;
        background-position: center !important;
        background-size: cover !important;
    }
}

.swiper-button-next {
    right: -90px !important;
    top: 250px !important;
    color: tomato !important;

    @media screen and (max-width: 1199px) {
        &::after {
            content: '' !important;
        }
    }
}

.swiper-button-prev {
    left: -90px !important;
    top: 250px !important;
    color: tomato !important;

    @media screen and (max-width: 1199px) {
        &::after {
            content: '' !important;
        }
    }
}

.swiper-button-next.swiper-button-disabled,
.swiper-button-prev.swiper-button-disabled {
    pointer-events: visible !important;
}
