.header {
    padding-top: 40px;
    padding-bottom: 40px;
    background-image: url(../images/header_mobile.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include retina {
        background-image: url(../images/header_mobile@2x.jpg);
    }
    @include tablet {
        background-image: url(../images/header_tablet.jpg);
        @include retina {
            background-image: url(../images/header_tablet@2x.jpg);
        }
    }
    @include desktop {
        background-image: url(../images/header_desktop.jpg);
        @include retina {
            background-image: url(../images/header_desktop@2x.jpg);
        }
    }
}
.logo {
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.17;
    color: $whiteColor;

    @include desktop {
        gap: 10px;
    }
    &__img {
        animation: animate360 3000ms cubic-bezier(0.25, 0.1, 0.25, 1) infinite;
    }

    &__text {
        display: none;
        @include tablet {
            display: block;
        }
    }
}
.navigation {
    font-weight: 500;
    line-height: 1.17;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $whiteColor;

    &__list {
        display: flex;
        gap: 20px;
        text-transform: uppercase;
    }
}
.link--current {
    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: -2px;
        width: 100%;
        height: 3px;
        margin: inherit;
        display: block;
        background-color: $secondAccentColor;
    }
}
.header-search {
    @extend %flexCentered;
    position: relative;
    margin: 37px auto 0 auto;
    @include tablet {
        margin-top: 50px;
    }
    &__label {
        position: relative;
        display: block;
    }
    &__input {
        min-width: 250px;
        width: 100%;
        height: 20px;
        font-size: 14px;
        line-height: 1.17;
        display: flex;
        align-items: center;
        color: $whiteColor;
        background-color: transparent;
        border: none;
        border-bottom: 0.5px solid $whiteColor;
        outline: none;
        &::placeholder {
            color: $whiteColor;
        }
    }
    &__btn-mic {
        margin-right: 8px;
        background-color: transparent;
        border: transparent;
        padding: 0;
        color: $whiteColor;
        transition: transform 500ms linear;
    }
    &__icon-mic {
        fill: currentColor;
    }

    &__icon-record {
        margin-right: 8px;
        fill: red;
        background-color: transparent;
        border: transparent;
    }
    &__icon {
        position: absolute;
        width: 16px;
        right: 0;
        bottom: 0px;
        fill: $whiteColor;
    }
}

.filter {
    width: 150px;
    margin: 0 auto 0 auto;
    text-align: center;

    &__title {
        @include font(14, 500, 1.5, 0.02em);
        color: white;
        margin-top: 20px;
    }
    &__checkbox {
        color: #fff;
    }
    &__input {
        margin-bottom: 10px;
        @include font(12, 500, 1.15, 0.02em);
        text-align: center;
        display: block;
        color: $mainColor;
        padding: 5px 30px 3px 20px;
        width: 150px;
        box-sizing: border-box;
        width: 100%;
        border: 1px solid $secondAccentColor;
        box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
        border-radius: 6px;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        // background-color: blue;
        background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
            linear-gradient(to bottom, $secondAccentColor 0%, #eb5a06a2 100%);
        background-repeat: no-repeat, repeat;
        background-position: right 5px top 58%, 0;
        background-size: 14px auto, 100%;
        @media screen and (max-width: 767px) {
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }

        &__input::-ms-expand {
            display: none;
        }
        &__input:hover {
            border-color: rgb(111, 111, 111);
        }
        &__input:focus {
            border-color: #aaa;
            box-shadow: 0 0 1px 1px rgb(231, 3, 3);
            box-shadow: 0 0 0 3px -moz-mac-focusring;
            color: #222;
            outline: none;
        }
        &__input option {
            font-weight: 500;
        }
    }
}

.-active {
    animation: fire-color 700ms linear infinite alternate;
}
@keyframes fire-color {
    0% {
        color: $whiteColor;
    }
    100% {
        transform: scale(1.7);
        color: $accentColor;
    }
}

.header-my-library {
    height: 230px;
    padding-top: 40px;
    background-image: url(../images/my_library_mobile.jpg),
        linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include retina {
        background-image: url(../images/my_library_mobile@2x.jpg),
            linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
    }
    @include tablet {
        height: 219px;
        background-image: url(../images/my_library_tablet.jpg),
            linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
        @include retina {
            background-image: url(../images/my_library_tablet@2x.jpg),
                linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
        }
    }
    @include desktop {
        height: 216px;
        background-image: url(../images/my_library_desktop.jpg),
            linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
        @include retina {
            background-image: url(../images/my_library_desktop@2x.jpg),
                linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
        }
    }
}
